<template>
  <modal :name="name" class="black-text" classes="v--modal bg-color-4 modal-add-tally z-depth-4"
         :width="350" height="auto" :reset="true" @before-open="beforeOpen">

    <div class="v--modal-header border-bottom border-white text-white">
      <div class="w-100 text-center py-3">
        <h5 class="modal-title w-100" id="modalSetTimeLabel">Modification des horaires</h5>
      </div>
    </div>

    <div class="v--modal-body py-4">
      <form id="formSetTally" @submit.prevent="save">

        <div class="col-12 my-3 text-center open-sans">
          <label class="h6 text-uppercase text-white">arrivée</label>
        </div>

        <div class="row" v-if="tally && tally.start">
          <ssf-form-group type="date" name="startDate" v-model="tally.start.date" col="col-12 col-md-6" label="Date"/>
          <ssf-form-group type="time" name="startTime" v-model="tally.start.time" col="col-12 col-md-6" label="Heure"/>
        </div>

        <div class="row" v-if="tally && tally.end">
          <div class="col-12 my-3 text-center open-sans">
            <label class="h6 text-uppercase text-white">départ</label>
          </div>
          <ssf-form-group type="date" name="endDate" v-model="tally.end.date" col="col-12 col-md-6" label="Date"/>
          <ssf-form-group type="time" name="endTime" v-model="tally.end.time" col="col-12 col-md-6" label="Heure"/>
        </div>
      </form>
    </div>

    <vue-modal-footer @cancel="hide" @save="save"/>

  </modal>
</template>

<script>
  import Tally          from "@/app/models/tally";
  import VueModalFooter from "@/components/includes/commons/partials/VueModalFooter";

  import moment from 'moment-timezone'

  export default {
    name: "ModalSetTally",

    props: {
      name: { type: String, required: false, default: 'modal-set-tally' }
    },

    components: { VueModalFooter },

    data() {
      return {
        tally: { start: null, end: null },
        id   : null
      }
    },

    methods: {
      hide() {
        this.$modal.hide(this.name)
      },

      beforeOpen(events) {
        if (events.params && events.params.tallyProp) {
          let tally = events.params.tallyProp;
          this.tally = {
            id   : tally.id,
            start: {
              date: this.helpers.date.momentSql(tally.start).format('DD/MM/YYYY'),
              time: this.helpers.date.momentSql(tally.start).format('HH:mm:ss'),
            },
            end  : tally.end ? {
              date: this.helpers.date.momentSql(tally.end).format('DD/MM/YYYY'),
              time: this.helpers.date.momentSql(tally.end).format('HH:mm:ss'),
            } : null,
          }
        }
      },

      save() {
        let data = {
          start: moment.tz(`${this.tally.start.date} ${this.tally.start.time}`, 'DD/MM/YYYY HH:mm:ss', 'Europe/Paris').add(2, 'hours'),
          end  : this.tally.end ? moment.tz(`${this.tally.end.date} ${this.tally.end.time}`, 'DD/MM/YYYY HH:mm:ss', 'Europe/Paris').add(2, 'hours') : null
        };
        Tally.update(this.tally.id, data).then(() => {
          this.$emit('updated', this.tally.id, this.tally);
          return this.hide()
        })
      }
    }
  }
</script>

<style lang="scss">
  .v--modal.modal-add-tally {
    overflow: visible !important;

    .ssf-text-container {
      color: #fff !important;
    }

    .ssf-form-control {
      padding: 14px 18px !important;
    }

    .ssf-form-group .ssf-form-control:focus + label, .ssf-form-group label.active {
      margin-left: 15px !important;
      font-size: 12px !important;
      border-radius: 4px !important;
      background-color: #12151c !important;
    }

    .control {
      display: none !important;
    }
  }
</style>
