<template>
  <div class="bg-transition full-flex" :class="{'w-small position-relative': helpers.window.isSmall()}"
       :id="`${image}BgTransition`" :style="`background-image: url('${src(image)}')`">
    <label class="h1 text-white text-uppercase indie-flower text-center" id="title">{{ title }}</label>
    <img :alt="image" class="bg-img" :src="src(image)" v-if="helpers.window.isSmall()">
  </div>
</template>

<script>
  export default {
    name: "BgTransition",

    props: {
      image: { type: String, required: true },
      title: { type: String, required: true },
    },

    methods: {
      src(image) {
        try {
          return image ? require(`@/assets/img/backgrounds/${image}.jpg`) : image
        } catch (e) {
          return image
        }
      }
    }
  }
</script>

<style lang="scss">

  .bg-transition {
    position: relative;
    width: 100%;
    height: 300px;


    &:not(.w-small) {
      background-repeat: no-repeat;
      background-attachment: fixed;
      -webkit-background-size: cover;
      background-size: cover;
    }

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(25, 29, 38, .5);
    }

    &.w-small {
      background: none !important;
    }

    img.bg-img {
      height: 300px;
      position: absolute;
      z-index: -2;
      margin: auto;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }

    #title {
      font-weight: 700;
      font-family: 'Lato', sans-serif;
      z-index: 2;
    }
  }

</style>
