<template>
  <div id="app">
    <!--    <h1>POUR LA PAGE DIVERS, METTRE LES APERCUS DANS DES IPHONES CSS</h1>-->
    <!--    <h1>COMMENCER LA PARTIE PLANNING (traduction de pointage pour créer la table)</h1>-->
    <!-- APPLICATIN IS UP -->
    <div class="h-100 w-100" v-if="!maintenance">
      <x-sidebar v-if="sidebarVisible()"/>

      <!-- MAIN CONTAINER -->
      <div class="main-container">
        <!--        <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">-->
        <div class="px-0 position-relative main pb-5" :class="{'col-md-10 col-lg-11 ml-auto': sidebarVisible()}">
          <router-view v-if="loaded" class="main-container"/>
        </div>
        <!--        </transition>-->
      </div>

      <!-- PRE LOAD -->
      <pre-load/>

      <!-- FORM LOAD-->
      <form-load/>

      <!-- FOOTER -->
      <!--      <x-footer/>-->
    </div>

    <!-- MAINTENANCE MODE -->
    <maintenance v-else-if="maintenance"/>

  </div>
</template>

<script>

  import FormLoad    from '@/components/includes/commons/FormLoad'
  import XSidebar    from "@/components/includes/commons/partials/XSidebar";
  import PreLoad     from '@/components/includes/commons/PreLoad'
  import XFooter     from '@/components/includes/commons/partials/XFooter'
  import Maintenance from '@/components/includes/Maintenance'

  export default {
    name: 'App',

    metaInfo: {
      // if no subcomponents specify a metaInfo.title, this title will be used
      title        : 'Wovalen',
      // all titles will be injected into this template
      titleTemplate: '%s | Wovalen'
    },

    components: {
      XSidebar,
      Maintenance,
      FormLoad,
      PreLoad,
      XFooter,
    },

    data() {
      return {
        // loaded: false,
      }
    },

    computed: {
      loaded() {
        return !this.$store.getters.preload
      },

      maintenance() {
        return this.$store.getters.APP_MAINTENANCE && (this.$store.getters.APP_MAINTENANCE === 'true' || this.$store.getters.APP_MAINTENANCE === true)
      }
    },

    mounted() {
    },

    methods: {

      sidebarVisible() {
        let authRoutes = _.filter(this.$router.options.routes, route => route.component && route.component.__file && route.component.__file.includes('Auth'))
        return this.$route.name && !_.map(authRoutes, route => route.name).includes(this.$route.name)
      },

      onCustomAction(action) {
        return eval(this[action]())
      },

      navigate(name) {
        return this.$router.push({ name })
      },

    }
  }
</script>

