<template>
  <div id="preload" class="position-fixed justify-content-center align-items-center bg-color-5" :class="className">
    <!--    <triple-spinner/>-->
    <img src="@/assets/img/logo.png">
  </div>
</template>

<script>

  let interval

  import TripleSpinner from "./Loader/TripleSpinner";

  export default {
    name: "PreLoad",

    components: {
      TripleSpinner,
    },

    data() {
      return {
        className: "d-flex",
        animate  : true
      }
    },

    created() {
      this.run()
    },

    methods: {

      close() {
        this.className = "d-flex animated fadeOut"
        setTimeout(() => {
          this.className = "d-none"
          clearInterval(interval)
          this.$store.dispatch('preloading', false)
        }, 1000)
      },

      run() {
        let self = this, i = 0
        interval = setInterval(() => {
          if ((document.readyState === "complete" || document.readyState === "ready") || i++ > 10)
            self.close()
        }, 1000)

      }
    }
  }
</script>

<style scoped lang="scss">

  #preload {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 16777271;
    background-color: #fff;

    img {
      /*height: 30%;*/
      /*width: 30%;*/
      -webkit-animation: rotate 1s linear infinite reverse;
      -o-animation: rotate 1s linear infinite reverse;
      animation: rotate 1s linear infinite reverse;
    }

    @keyframes rotate {
      from {
        -webkit-transform: rotate(0);
        -moz-transform: rotate(0);
        -ms-transform: rotate(0);
        -o-transform: rotate(0);
        transform: rotate(0);
      }
      to {
        -webkit-transform: rotate(360deg);
        -moz-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
        -o-transform: rotate(360deg);
        transform: rotate(360deg);
      }
    }
  }


</style>
