<template>
  <div id="kolyskoIndex">
    <bg-transition image="kolysko" title="Congés"/>

    <ssf-calendar :today="date" :year="year" :holidays="holidays" @nav-date="onNavDate" @selected="onSelected"
                  @date-picked="onDatePicked"/>

    <div class="row mt-5 mt-md-4" id="legend" v-if="categories">
      <div class="col-2 ol-md-2 text-center mb-1" v-for="category in categories">
        <div class="type d-block m-auto rounded-circle pb-2">
          <span class="full-flex mx-auto category-cell" :style="`background: ${category.color};`" data-toggle="tooltip"
                data-placement="top" :data-original-title="category.label.capitalize()">
            <span class="text-white">{{ category.label.split(' ').map(word => word[0]).join('') | uppercase }}</span>
          </span>
        </div>
        <span class="font-italic small" :style="`color: ${category.color}`">{{ perCategory(category) }}</span>
      </div>
    </div>

    <btn-floating-container @pick-date="onPickDate" @export="onExport"/>

    <modal-add-holiday :categories="categories" :holidays="holidays" @refresh="fetchHolidays"/>

    <modal-pick-date @date-picked="onDatePicked"/>

  </div>
</template>

<script>
  import Category        from "@/app/models/kolysko/category";
  import Holiday         from "@/app/models/kolysko/holiday";
  import Period          from "@/app/models/kolysko/period";
  import BgTransition    from "@/components/includes/commons/partials/BgTransition";
  import SsfCalendar     from "@/components/pages/Planning/components/includes/Kolysko/Calendar";
  import BtnFloatingContainer
                         from "@/components/pages/Planning/components/includes/Kolysko/includes/BtnFloatingContainer";
  import ModalAddHoliday from "@/components/pages/Planning/components/includes/Kolysko/includes/ModalAddHoliday";
  import ModalPickDate   from "@/components/pages/Planning/components/includes/Kolysko/includes/ModalPickDate";

  export default {
    name: "Kolysko",

    components: { ModalPickDate, ModalAddHoliday, BtnFloatingContainer, SsfCalendar, BgTransition },

    data() {
      return {
        categories: null,
        holidays  : null,
        period    : null,

        year: null,
        date: null
      }
    },

    created() {
      return this.run()
    },

    computed: {
      /*      height() {
              return this.helpers.window.isSmall() ? 667 : $('.main-container .main').width()
            },
            width() {
              return this.helpers.window.isSmall() ? this.helpers.window.width() : $('.main-container .main').width()
            }*/
    },

    methods: {
      async run() {
        this.date = this.helpers.date.moment();
        this.year = this.date.year();
        this.categories = await Category.all();
        this.period = await Period.mine();
        this.fetchHolidays();
        $('[data-toggle="tooltip"]').tooltip()
      },


      async fetchHolidays() {
        this.holidays = await Holiday.period(this.date, this.year)
      },

      perCategory(category) {
        return this.holidays ? _.filter(this.holidays, holiday => holiday.category.id === category.id).length - _.filter(this.holidays, holiday => holiday.category.id === category.id && holiday.half).length / 2 : 0
      },

      onNavDate(date, direction) {
        this.date = date
        this.year = this.date.year()
        if ((direction < 0 && parseInt(this.date.format('MM')) === parseInt(this.period.end.split('-')[0]))
          || (direction > 0 && parseInt(this.date.format('MM')) === parseInt(this.period.start.split('-')[0]))
          || direction === 0)
          this.fetchHolidays()
      },

      onExport() {
        let filename = `conges.${this.helpers.date.moment().format('DDMMYYHHmmss')}.xlsx`//Carbon::now()->format('YmdHis')
        Holiday.export(this.date, this.year).then((response) => this.downloadFile(response, filename))
      },

      downloadFile(response, filename) {
        let newBlob = new Blob([response.data]);
        if (window.navigator && window.navigator.msSaveOrOpenBlob) {
          window.navigator.msSaveOrOpenBlob(newBlob);
          return
        }
        const data = window.URL.createObjectURL(newBlob);
        let link = document.createElement('a');
        link.href = data;
        link.download = filename;
        link.click();
        setTimeout(function () {
          // For Firefox it is necessary to delay revoking the ObjectURL
          window.URL.revokeObjectURL(data)
        }, 100)
        return true
      },

      onPickDate() {
        this.$modal.show('modal-pick-date')
      },

      onDatePicked(date) {
        this.onNavDate(date, 0)
      },

      onSelected(date) {
        this.$modal.show('modal-add-holiday', {
          date
        })
      }
    }

  }
</script>

<style lang="scss">
  #kolyskoIndex {
    .date-cell.week-end:not(.cell-previous-month) {
      /*color: #fff;*/
      background: #FF3B3F;
      background-image: repeating-linear-gradient(-45deg, #1b222c 3px, #1b222c 6px, #FF3B3F 7px, #FF3B3F 14px);
    }

    .date-cell.out:not(.cell-previous-month) {
      /*color: #fff;*/
      background: #ff0;
      background-image: repeating-linear-gradient(-45deg, #1b222c 3px, #1b222c 6px, #ff0 7px, #ff0 14px);
    }

    .date-cell.half {
      position: relative;

      &:before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        height: 3px;
        background: #FFF;
        transform: rotate(-45deg);
      }

      /*border: 2px dotted #000 !important;*/
    }

    .category-cell {
      font-weight: 500;
      height: 35px;
      width: 35px;
      border-radius: 50%;

      &, & * {
        cursor: default;
      }
    }
  }
</style>
