<template>
  <div id="homeIndex">
    <button @click="run">re-run</button>
    {{ stats }}
  </div>
</template>

<script>

  import Model from "@/app/models/model";

  export default {
    name      : "HomeIndex",
    components: {},
    metaInfo  : {
      title: 'Accueil',
    },

    data() {
      return {
        stats: null
      }
    },

    created() {
      this.$router.push({ name: 'life.index' })
    },

    methods: {
      async run() {
        this.stats = await Model.all('/stats')
      }
    }

  }
</script>

