<template>
  <div class="row w-100 full-flex" id="amountContainer" @click="$refs.input.focus()">

    <form @submit.prevent="more" autocomplete="off">

      <div class="row">

        <div class="col-12">
          <div class="ssf-form-group text-center">
            <label for="amount" class="sr-only">Montant</label>
            <input type="text" name="amount" id="amount" v-model="amount" @input="formatNumber()"
                   class="border-0 text-center fa-5x text-white font-weight-light bg-transparent" ref="input">
          </div>
        </div>

      </div>

      <div class="row d-block text-center">

        <!--        <div class="col-6 text-center grey-text">-->
        <ssf-icon icon="eraser" type="fa" size="2x" class="text-danger cursor-pointer mx-3" @click="del()" weight="light"/>
        <!--        </div>-->

        <!--        <div class="col-6">-->
<!--        <div class="ssf-form-group text-center">-->
          <ssf-icon icon="layer-plus" type="fa" size="2x" class="text-primary cursor-pointer mx-3" role="button"
                    @click="more()" weight="light"/>
<!--        </div>-->
        <!--        </div>-->

      </div>

    </form>

    <div class="row col-12">
      <div class="ssf-form-group text-center grey-text">
        <span class="font-italic">{{ sum }}</span>
        <span class="mx-1">/</span>
        <span class="fa-2x text-white">{{ Math.ceil(sum) }}<ssf-icon icon="euro-sign" type="la" size="xs"/></span>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "AmountContainer",

    data() {
      return {
        amount : 0,
        amounts: []
      }
    },

    computed: {
      sum() {
        return (parseFloat(
          (
            (
              (
                (this.amounts.length * 100) - _.reduce(this.amounts, (a, b) => a + parseInt(b), 0)//this.amounts.reduce((sum = 0, b) => sum + parseInt(b))
              ) / 100
            )
          )
        ) * 2).toFixed(2)
      }
    },

    methods: {
      del() {
        this.$localStorage.remove('amounts');
        this.$localStorage.remove('date');
        this.amounts = []
      },

      fetch() {
        let tmp, tmp2;

        if ((tmp = this.$localStorage.get('amounts')) === null || tmp === '' || (tmp2 = this.$localStorage.get('date')) === null || tmp2 === '' || new Date(tmp2).toDateString() !== new Date().toDateString()) {
          if (new Date(tmp2).toDateString() !== new Date().toDateString()) this.del();
          return []
        } else return JSON.parse(atob(tmp))
      },

      more() {
        if (this.amount) {
          _.split(this.amount, ';').forEach((amount) => this.add(amount));
          this.amount = null
        }
      },

      add(n) {
        if (this.isInt(n) || Math.floor(parseFloat(n)) === 0) this.store(parseFloat(n));
        else this.store(Math.floor((n - Math.floor(parseFloat(n))) * 100))
      },

      store(amount) {
        this.amounts.push(parseFloat(amount));
        // this.amounts.push(parseFloat(amount) / 2)
        this.$localStorage.set('amounts', btoa(JSON.stringify(this.amounts)));
        this.$localStorage.set('date', new Date().toDateString())
      },

      isInt(n) {
        return Number(n) === parseInt(n) && parseInt(n) % 1 === 0;
      },

      formatNumber() {
        this.amount = this.amount.replace(',', '.')
      },

      getSum() {
        return this.amounts.length === 0 ? 0 : parseFloat(((((this.amounts.length * 100) - this.amounts.reduce((sum = 0, b) => sum + parseInt(b))) / 100)).toFixed(2))
      },

    }
  }
</script>

