import cookie                  from "json-cookie";
import api, {AuthApi, MainApi} from "../http/api";
import store                   from "../store";
import {jsonParse}             from "../utils";

class User {

  static storeToken(token) {
    cookie.set(store.getters.TOKEN_NAME, token.content, {
      secure : false,
      expires: (parseInt(token.expires) / 86400) + 1
    });
  }

  static deleteToken() {
    cookie.delete(store.getters.TOKEN_NAME);
  }

  static storeLocalStorage(user) {
    localStorage.setItem(`${store.getters.storage.prefix}user`, JSON.stringify(user));
  }

  static deleteLocalStorage() {
    localStorage.removeItem(`${store.getters.storage.prefix}user`);
  }

  static setTokenAndStorage(token, user) {
    this.storeToken(token);
    this.storeLocalStorage(user);
  }

  static deleteTokenAndStorage() {
    this.deleteToken();
    this.deleteLocalStorage();
  }

  static isStrongPassword(password) {
    return /(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*?[\/+~"'{}()|#`_\[\]=%§.,:;?!@$^&*-]).{8,}/.test(password);
  }

  static token() {
    return cookie.get(store.getters.TOKEN_NAME) || null;
  }

  static storage() {
    return jsonParse(localStorage.getItem(`${store.getters.storage.prefix}user`));
  }

  static authenticate() {
    return this.token() ? !(this.token().trim() === '') : false;
  }

  static async database() {
    return await (new MainApi()).get('/user/current')
      .then((data) => data.user || null)
      .catch(() => null)
  }

  static async logged() {
    return this.token() && await (new MainApi()).get('/logged')
      .then((data) => !!data.logged)
      .catch(() => false);
  }
}

export default User
