<template>
  <div id="tallyIndex">
    <bg-transition image="tally" title="Pointage"/>

    <!--    <button @click="run">re-run</button>-->

    <div class="row full-flex" id="firstTallyContainer">

      <div class="col-12 col-md-4 my-3 my-md-0">
        <clock/>
      </div>

      <div class="col-12 col-md-5 text-center fa-3x my-3 my-md-0 d-md-none">
        <div class="animated zoomIn" v-show="time && time > 0">
          <span class="number">{{ duration }}</span>
        </div>
      </div>

      <div class="col-12 col-md-8">
        <line-chart :chart-data="collection" v-if="collection" :height="250" class="bg-color-4 rounded"
                    :options="{responsive: true, maintainAspectRatio: false, scales: { yAxes: [{ display: true, ticks: { beginAtZero: true, max: Math.max(...collection.datasets[0].data), min: Math.min(...collection.datasets[0].data) } } ] }}"/>
      </div>

      <div class="col-12 col-md-7 my-3">
        <infos @start="start" @stop="stop" :working="tallies.working" :time="time" class="bg-color-4 py-2 rounded"/>
      </div>

      <div class="col-12 col-md-5 text-center fa-3x my-3 my-md-0 d-none d-md-block">
        <div class="animated zoomIn" v-show="time && time > 0">
          <span class="number">{{ duration }}</span>
        </div>
      </div>

    </div>

    <div class="row w-100">

      <div class="card w-100 rounded-0 z-depth-0">

        <div class="card-header text-white rounded-0 bg-color-2 border-bottom border-color-4 position-relative">
          <h5 class="card-title text-center">{{ date.format('MMMM') | capitalize }}</h5>
          <h6 class="card-title text-center">{{ date.format('YYYY') | capitalize }}</h6>

          <div class="month-cursor">
            <ssf-icon icon="chevron-left" size="2x" type="la" class="month-cursor-item month-cursor-previous"
                      @click="navigate(-1)"/>
            <ssf-icon icon="chevron-right" size="2x" type="la" class="month-cursor-item month-cursor-next"
                      @click="navigate()"/>
          </div>
        </div>


        <div class="card-body p-0">

          <table
            class="col-12 text-center table-timeline table text-white m-0 table-striped table-sm table-hover table-borderless">
            <thead class="bg-color-4">
              <tr class="row">
                <th v-for="row in html.table.headers" :class="row.cssClass" class="text-center">
                  <ssf-icon :icon="row.icon" type="la" class="fa-1x5"/>
                </th>
              </tr>
            </thead>


            <tbody v-if="tallies.all" class="bg-color-5">
              <!--              {{ tallies.computed }}-->
              <tr v-for="(tally, index) in monthTallies/*tallies.computed*/" class="row"
                  :class="helpers.window.isSmall() ? 'small' : ''" :key="index">
                <th scope="row" class="col-1 px-0 text-info text-center">{{ index + 1 }}</th>

                <td class="col-2" :class="{'px-0': helpers.window.isSmall()}">
                  <span class="font-italic small">
                    {{ helpers.date.momentSql(tally.start).format(helpers.window.isSmall() ? 'ddd' : 'dddd') | capitalize }}
                  <span class="number">{{ helpers.date.momentSql(tally.start).date() }}</span>
                  </span>
                </td>

                <td class="col-3 text-center number" @click="$modal.show('modal-set-tally', {tallyProp: tally})"
                    v-if="helpers.window.isSmall()">
                  {{ helpers.date.momentSql(tally.start).format('H:mm').replace(':', 'h') }}
                </td>

                <td class="col-3 text-center number" v-on:dblclick="$modal.show('modal-set-tally', {tallyProp: tally})"
                    v-else>
                  {{ helpers.date.momentSql(tally.start).format('H:mm').replace(':', 'h') }}
                </td>

                <td class="col-3 text-center cursor-pointer number full-flex" v-if="tally.end === null" @click="stop">
                  <ssf-icon icon="cog" class="la-spin la-fw" type="la" size="lg"/>
                </td>

                <td class="col-3 text-center number" v-else>
                  {{ helpers.date.momentSql(tally.end).format('H:mm').replace(':', 'h') }}
                </td>

                <td class="col-3 text-center number">
                  {{ tally.end ? format.time(diff(tally.start, tally.end, tally.lunch)) : duration }}
                </td>

              </tr>

              <!--<tr v-for="i in html.table.max" v-if="monthTallies.length + i < html.table.max + 1" class="row">
                <th scope="row" class="col-1 text-info text-center px-0">{{ monthTallies.length + i }}</th>
                <td class="col-2"></td>
                <td class="col-3"></td>
                <td class="col-3"></td>
                <td class="col-3"></td>
              </tr>-->
            </tbody>

            <tfoot class="bg-color-4">
              <tr class="row">
                <th scope="row" class="col-2 text-left font-italic small">Total</th>
                <td class="col-1"></td>
                <td class="col-3"></td>
                <td class="col-3"></td>
                <th class="col-3 h6 number">{{ total }}</th>
              </tr>
            </tfoot>
          </table>

        </div>
      </div>

    </div>

    <modal-set-tally @updated="onUpdated"/>

  </div>
</template>

<script>
  import Tally         from "@/app/models/tally";
  import helpers       from "@/app/utils/helpers";
  import BgTransition  from "@/components/includes/commons/partials/BgTransition";
  import Clock         from "@/components/pages/Planning/components/includes/Tally/Clock";
  import Infos         from "@/components/pages/Planning/components/includes/Tally/Infos";
  import ModalSetTally from "@/components/pages/Planning/components/includes/Tally/ModalSetTally";
  import LineChart     from "@/components/includes/commons/charts/LineChart";

  let interval;

  export default {
    name      : "Index",
    components: { ModalSetTally, Infos, Clock, BgTransition, LineChart },

    data() {
      return {
        tallies: { all: null, working: null, computed: null },
        time   : 0,


        currentMonth: null,
        currentYear : null,

        date: helpers.date.moment(),

        collection: null,

        html  : {
          table: {
            headers: [
              { icon: 'hashtag', cssClass: 'col-1 px-0 blue-text' },
              { icon: 'calendar-alt', cssClass: 'col-2 red-text' },
              { icon: 'business-time', cssClass: 'col-3 green-text' },
              { icon: 'plane-departure', cssClass: 'col-3 yellow-text' },
              { icon: 'ethernet', cssClass: 'col-3 blue-grey-text' },
            ],
            offset : 0,
            limit  : 20,
            max    : 20
          }
        },
        format: {
          time(seconds) {
            let time = {
              hours  : helpers.number.twoDigits(parseInt(seconds / 60 / 60)),
              minutes: helpers.number.twoDigits(parseInt((seconds / 60) % 60)),
              seconds: helpers.number.twoDigits(parseInt(seconds % 60))
            };
            return `${time.hours}:${time.minutes}:${time.seconds}`
          }
        }
      }
    },

    created() {
      return this.run()
    },

    computed: {
      duration() {
        if (this.time) {
          let time = {
            hours  : this.helpers.number.twoDigits(parseInt(this.time / 60 / 60)),
            minutes: this.helpers.number.twoDigits(parseInt((this.time / 60) % 60)),
            seconds: this.helpers.number.twoDigits(parseInt(this.time % 60))
          };
          return this.tallies.working ? `${time.hours}:${time.minutes}:${time.seconds}` : null
        }
      },

      monthTallies() {
        this.$forceUpdate();
        return _.filter(this.tallies.all, tally => this.helpers.date.moment(tally.start).month() === this.helpers.date.moment(this.date).month())
      },

      total() {
        return this.format.time(_.map(this.monthTallies, tally => tally.end ? this.diff(tally.start, tally.end, tally.lunch) : 0).reduce((a, b) => a + b, 0))
      }
    },

    methods: {
      async fetchTallies() {
        this.tallies.all = await Tally.mine();
        this.tallies.computed = _.filter(this.tallies.all, tally => this.helpers.date.moment(tally.start).month() === this.helpers.date.moment(this.date).month())
        this.updateCollection()
      },

      async fetchWorking() {
        let working = await Tally.working();
        this.tallies.working = working && working.data ? working.data.data : working;
        interval = setInterval(() => {
          this.time = this.diff()
        }, 1000)
      },

      diff(start = null, end = null, lunch = null) {
        if (this.tallies.working === null && start === null && end === null)
          return null;
        else {
          // let d = this.moment.duration(((end === null ? this.moment() : this.moment(end)).diff(this.moment(start || this.today.start))))
          let duration = this.helpers.date.momentSql(end).diff(this.helpers.date.momentSql(start || this.tallies.working.start), 'seconds');

          if (this.onlyAfterNoon(start) && ((end && this.helpers.date.momentSql(end).hours() >= 14) || (this.tallies.working && this.helpers.date.moment().hours() >= 14)))
            duration = end && lunch ? duration - lunch * 60 : this.tallies.working ? duration - this.tallies.working.lunch * 60 : null;

          return duration || 0
        }
      },

      onlyAfterNoon(start) {
        return start ? this.helpers.date.momentSql(start).hours() <= 12 : (this.tallies.working && this.helpers.date.momentSql(this.tallies.working.start).hours() <= 12)
      },

      async start() {
        await Tally.start();
        await this.fetchWorking();
        this.tallies.all.unshift(this.tallies.working)
      },

      stop() {
        if (this.tallies.working) {
          Tally.stop(this.tallies.working.id).then(() => {
            this.tallies.working.end = this.helpers.date.moment();
            _.filter(this.tallies.all, tally => tally.end = tally.id === this.tallies.working.id ? this.tallies.working.end : tally.end);
            clearInterval(interval);
            this.time = 0
          });
        }
      },

      navigate(direction = 1) {
        this.date.add(direction, 'month')
      },

      run() {
        this.fetchTallies();
        this.fetchWorking();
        this.$forceUpdate()
      },

      onUpdated(id, tally) {
        this.fetchTallies()
      },

      updateCollection() {
        // console.log(this.time)
        if (this.tallies.all) {
          this.collection = {
            labels    : _.map(this.tallies.all, tally => this.helpers.date.momentSql(tally.start).format('L')),
            labelColor: '#fff',
            datasets  : [{
              label          : "Temps de travail",
              backgroundColor: 'rgba(188, 80, 144, .6)',
              borderColor    : '#bc5090',
              data           : _.map(this.tallies.all, tally => this.diff(tally.start, tally.end || this.helpers.date.momentSql(), tally.lunch) / 60 / 60)
            }]
          }
        }
      },
    }
  }
</script>

<style scoped lang="scss">
  $bgHeight: 300px;
  #tallyIndex {
    #firstTallyContainer {
      min-height: calc(100vh - #{$bgHeight});
    }

    @media screen and (max-width: 768px) {
      .table-timeline {
        td {
          /*font-size: 10px;*/
        }
      }
    }


    .month-cursor {
      > .month-cursor-item {
        position: absolute;
        top: 0;
        bottom: 0;
        height: 30px;
        margin-top: auto;
        margin-bottom: auto;
        cursor: pointer;

        &.month-cursor-previous {
          left: 20px;
        }

        &.month-cursor-next {
          right: 20px;
        }
      }
    }

  }
</style>
