<template>
  <modal :name="name" class="black-text" classes="v--modal bg-color-4 modal-edit-expense z-depth-4"
         :width="350" height="auto" :reset="true" @before-open="beforeOpen">

    <div class="v--modal-header border-bottom border-white">
      <div class="w-100 text-center py-3">
        <ssf-icon icon="euro-sign" size="4x" type="la" class="color-3"/>
      </div>
    </div>

    <div class="v--modal-body py-4">
      <!--      <span class="text-white">{{ expense }}</span>-->
      <form id="formEditExpense" @submit.prevent="save" class="text-white" @keyup.enter="save">
        <ssf-form-group type="text" name="label" v-model="expense.label" col="col-12" :row="true" label="Libellé"
                        input-class="text-white"/>
        <ssf-form-group type="number" name="amount" v-model="expense.amount" col="col-12" :row="true" label="Montant"
                        input-class="text-white"/>

        <!--<ssf-form-group type="number" name="meals" v-model="expense.meals_tickets" col="col-12" :row="true"
                        label="Tickets restaurants" input-class="text-white"/>-->
      </form>
    </div>

    <vue-modal-footer @cancel="hide" @save="save"/>

  </modal>
</template>

<script>
  import Salary         from "@/app/models/expense";
  import Expense        from "@/app/models/model";
  import VueModalFooter from "@/components/includes/commons/partials/VueModalFooter";

  export default {
    name: "ModalEditExpense",

    props: {
      name: { type: String, required: false, default: 'modal-edit-expense' }
    },

    components: { VueModalFooter },

    data() {
      return {
        expense: { date: null, amount: null },
        id     : null
      }
    },

    methods: {
      hide() {
        this.$modal.hide(this.name)
      },

      beforeOpen(events) {
        if (events.params && events.params.expenseProp)
          this.expense = events.params.expenseProp;
        else
          this.reset()
      },

      brutToNet() {
        return parseInt(((2750 * 0.7876221241727072) + ((36.9 / 2) * 4) - ((10 / 2) * 20) - (107 / 2)).toString())
      },

      reset() {
        this.expense = {
          date  : this.helpers.date.moment().format('YYYY-MM-DD'),
          amount: this.brutToNet(),
          meals : 15
        }
      },

      save() {
        if (this.expense.id)
          return Expense.update('/expense', this.expense, this.expense.id).then(() => {
            this.$emit('updated');
            return this.hide()
          })
      }
    }
  }
</script>

<style lang="scss">
  .v--modal.modal-edit-expense {
    overflow: visible !important;

    .ssf-text-container {
      color: #fff !important;
    }

    .ssf-form-control {
      padding: 14px 18px !important;
    }

    .ssf-form-group .ssf-form-control:focus + label, .ssf-form-group label.active {
      margin-left: 15px !important;
      font-size: 12px !important;
      border-radius: 4px !important;
      background-color: #12151c !important;
    }

    .control {
      display: none !important;
    }
  }
</style>
