<script>

  // import helpers from "../../app/helpers";

  export default {
    name: "XSidebar",

    data() {
      return {
        show : true,
        links: [
          // { label: "Accueil", name: "home", icon: 'home', },
          { label: "Planning", name: "planning.index", icon: 'calendar-alt', },
          { label: "Vie", name: "life.index", icon: 'child', },
          // { label: "Etna", name: "school.index", icon: 'laptop', },
          { label: "Divers", name: "other.index", icon: 'book', },
          { label: "Simulations", name: "simulate.index", icon: 'calculator', },
          { label: "Savey", name: "savey.index", icon: 'hand-holding-usd', },
          // { label: "Vêtements", name: "other.index", icon: 'tshirt', },
          // { label: "Yaghilaz", name: "other.index", icon: 'tv', },
        ]
      }
    },

    methods: {
      linkClass(link) {
        return (link.routes && link.routes.includes((this.$route.name ? this.$route.name.toLowerCase() : ''))) || (this.$route.name && link.name && link.name.toLowerCase() === this.$route.name.toLowerCase()) || (link.contains && this.$route.name.toLowerCase().includes(link.contains))
          ? 'active text-white bg-color-2'
          : ''
      },
    }
  }
</script>

