<template>
  <auth id="login">

    <template slot="error" v-if="error">{{ error }}</template>

    <template slot="form">
      <form id="formRegister" class="w-100" @submit.prevent="register()">

        <div class="row">

          <ssf-form-group col="col-12 col-lg-6" name="lastname" v-model="user.lastname" label="Nom" :required="true"
                          icon="user"/>

          <ssf-form-group col="col-12 col-lg-6" name="firstname" v-model="user.firstname" label="Prénom"
                          :required="true" icon="user"/>

        </div>

        <ssf-form-group :row="true" col="col-12" name="email" v-model="user.email" label="Adresse e-mail"
                        :required="true" icon="at"/>

        <ssf-form-group :row="true" col="col-12" name="password" v-model="user.password" label="Mot de passe"
                        type="password" :required="true" icon="lock-alt" :autocomplete="true"/>

        <ssf-form-group :row="true" col="col-12" name="confirm_password" v-model="user.confirm_password"
                        label="Confirmation mot de passe" type="password" :required="true" icon="lock-alt"
                        :autocomplete="true"/>

        <div class="row">
          <div class="col-12 ssf-form-group mt-1">
            <router-link :to="{name: 'login'}">
              <ssf-icon icon="arrow-left"/>
              J'ai déjà un compte
            </router-link>
          </div>
        </div>

        <div class="row">
          <div class="col-12 ssf-form-group text-right mt-1 mb-0">
            <button class="btn bg-color-1 btn-block" type="submit">Enregistrer</button>
          </div>
        </div>

      </form>
    </template>

  </auth>
</template>

<script>

  import {AuthApi} from "../../../app/http/api";
  import Auth      from "./Auth";

  export default {
    name: "Register",

    components: { Auth },

    metaInfo: { title: 'Inscription', },

    data() {
      return {
        error: null,
        user : { firstname: null, lastname: null, email: null, password: null, confirm_password: null }
      }
    },

    methods: {
      register() {
        this.error = null
        AuthApi.register(this.user).then(() => location.reload()).catch((error) => this.error = error)
      }
    }
  }
</script>

