<template>
  <modal :name="name" class="black-text" classes="v--modal bg-color-4 modal-add-salary z-depth-4"
         :width="350" height="auto" :reset="true" @before-open="beforeOpen">

    <div class="v--modal-header border-bottom border-white">
      <div class="w-100 text-center py-3">
        <ssf-icon icon="euro-sign" size="4x" type="la" class="color-3"/>
      </div>
    </div>

    <div class="v--modal-body py-4">
      <form id="formAddSalary" @submit.prevent="save">
        <ssf-form-group type="date" name="date" v-model="salary.date" col="col-12" :row="true" label="Date"/>
        <ssf-form-group type="number" name="amount" v-model="salary.amount" col="col-12" :row="true" label="Montant"
                        input-class="text-white"/>

        <ssf-form-group type="number" name="meals" v-model="salary.meals_tickets" col="col-12" :row="true"
                        label="Tickets restaurants" input-class="text-white"/>
      </form>
    </div>

    <vue-modal-footer @cancel="hide" @save="save"/>

  </modal>
</template>

<script>
  import Salary         from "@/app/models/salary";
  import VueModalFooter from "@/components/includes/commons/partials/VueModalFooter";

  export default {
    name: "ModalAddSalary",

    props: {
      name: { type: String, required: false, default: 'modal-add-salary' }
    },

    components: { VueModalFooter },

    data() {
      return {
        salary: { date: null, amount: null },
        id    : null
      }
    },

    methods: {
      hide() {
        this.$modal.hide(this.name)
      },

      beforeOpen(events) {
        if (events.params && events.params.salaryProp)
          this.salary = events.params.salaryProp;
        else
          this.reset()
      },

      brutToNet() {
        return parseInt(((2750 * 0.7876221241727072) + ((36.9 / 2) * 4) - ((10 / 2) * 20) - (107 / 2)).toString())
      },

      reset() {
        this.salary = {
          date  : this.helpers.date.moment().format('YYYY-MM-DD'),
          amount: this.brutToNet(),
          meals : 15
        }
      },

      save() {
        return (this.salary.id)
          ? Salary.update('/salary', this.salary, this.salary.id).then(() => {
            this.$emit('updated');
            return this.hide()
          })
          : Salary.store('/salary', this.salary).then(salary => {
            this.$emit('added', salary);
            return this.hide()
          })
      }
    }
  }
</script>

<style lang="scss">
  .v--modal.modal-add-salary {
    overflow: visible !important;

    .ssf-text-container {
      color: #fff !important;
    }

    .ssf-form-control {
      padding: 14px 18px !important;
    }

    .ssf-form-group .ssf-form-control:focus + label, .ssf-form-group label.active {
      margin-left: 15px !important;
      font-size: 12px !important;
      border-radius: 4px !important;
      background-color: #12151c !important;
    }

    .control {
      display: none !important;
    }
  }
</style>
