<template>
  <div class="tallyAction"></div>
</template>

<script>


  import Tally from "@/app/models/tally";

  export default {
    name: "Action",

    data() {
      return {
        action: null
      }
    },

    created() {
      return this.run()
    },

    methods: {
      run() {
        let actions = ['start', 'stop'];
        if (this.$route && this.$route.params && this.$route.params.action && actions.includes(this.$route.params.action)) {
          this.action = this.$route.params.action
          Tally[this.action]().then(() => window.close())
        }
      }
    }
  }
</script>

