<template>
  <auth id="resetPassword">

    <template slot="error" v-if="error">{{ error }}</template>

    <template slot="form">

      <form id="formResetPassword" class="w-100" @submit.prevent="send">

        <ssf-form-group :row="true" col="col-12" name="email" v-model="user.email" label="Adresse e-mail"
                        :required="true" icon="at"/>

        <ssf-form-group :row="true" col="col-12" name="password" v-model="user.password" label="Nouveau mot de passe"
                        type="password" :required="true" icon="lock-alt" :autocomplete="true"/>

        <ssf-form-group :row="true" col="col-12" name="confirm_password" v-model="user.confirm_password"
                        label="Confirmation mot de passe" type="password" :required="true" icon="lock-alt"
                        :autocomplete="true"/>

        <div class="row">
          <div class="col-12 ssf-form-group mt-1 mb-0">
            <button class="btn bg-color-1 btn-block" type="submit">Enregistrer la modification</button>
          </div>
        </div>
      </form>

    </template>
  </auth>
</template>

<script>
  import {AuthApi}   from "../../../../app/http/api";
  import {MainError} from "../../../../app/utils/swal";
  import Auth        from "../Auth";

  import sha1 from 'sha1'

  export default {
    name      : "PasswordReset",
    components: { Auth },

    metaInfo: { title: 'Réinitialisation mot de passe', },

    data() {
      return {
        user : {},
        error: null
      }
    },

    created() {
      this.run()
    },

    methods: {

      send() {
        this.error = null
        AuthApi.reset(this.user).then(() => location.reload()).catch((error) => this.error = error)
      },

      run() {
        try {
          if (this.$route.params.email) this.user.email = atob(this.$route.params.email)
        } catch (e) {
          MainError.fire()
        }
      }
    }
  }
</script>

