import Model from "@/app/models/model";

class Tally extends Model {
  static tableSingular = '/tally';
  static table = this.tableSingular;

  static mine() {
    return super.mine(this.table)
  }

  static start() {
    return super.store(this.table)
  }

  static stop(id) {
    return super.update(`${this.table}/stop`, null, id)
  }

  static working() {
    return super.fetchById(this.table, 'working')
  }

  static update(id, data) {
    return super.update(this.table, data, id)
  }
}

export default Tally
