import Model from "@/app/models/model";

class Salary extends Model {
  static tableSingular = '/salary';
  static table = `/salaries`;

  static mine() {
    return super.mine(this.table)
  }

  static lastYear() {
    return super.fetchById(this.table, 'lastYear')
  }

  static twoYearsAgo() {
    return super.fetchById(this.table, 'twoYearsAgo')
  }

  static tickets = {
    sum   : () => super.fetchById(this.table, 'tickets'),
    update: (sum) => super.update(`${this.table}/tickets`, { sum }, -1)
  }
}

export default Salary
