<template>
  <div class="infos-circle text-center">
    <div class="row w-100">
      <div class="col-6 col-md-4">
        <div class="infos-circle-item bg-color-2 z-depth-1 full-flex" v-on:dblclick="toggleShow('tickets')">
          <button class="btn btn-remove-ticket bg-color-5 red-text" @click="save(tickets.sum - 1)">-1</button>
          <span class="fa-2x" v-show="show.tickets.sum">{{ tickets.sum }}</span>
          <input v-show="show.tickets.input" v-model="tickets.sum" autofocus @keyup.enter="save()"
                 class="bg-transparent fa-2x text-white p-0 w-100 border-0 text-center">
        </div>
      </div>

      <div class="col-6 col-md-4">
        <div class="infos-circle-item bg-color-2 z-depth-1 full-flex cursor-pointer"
             @click="emit">
          <ssf-icon icon="stop" weight="sharp" type="material" v-if="show.working" style="font-size: 4rem"/>
          <ssf-icon icon="play_arrow" weight="sharp" type="material" v-else style="font-size: 4rem"/>
        </div>
      </div>

      <div class="col-md-4 mx-auto">
        <div class="infos-circle-item bg-color-2 z-depth-1 full-flex">
          <pie-chart :data="collection.data" :background="collection.background" :height="125" :width="125"
                     :options="{label: { display: false }, legend: { display: false }, animation: { duration: 0 }}"
                     v-if="collection"/>
        </div>
      </div>

    </div>

  </div>
</template>

<script>
  import Salary   from "@/app/models/salary";
  import PieChart from "@/components/includes/commons/charts/PieChart";
  import moment   from "moment";

  export default {
    name      : "Infos",
    components: { PieChart },
    props     : {
      working: { type: Object | null, required: true },
      time   : { type: Number | null, required: true },
    },

    data() {
      return {
        tickets: {},

        show: { tickets: { sum: true, input: false }, working: false },

        collection: {
          background: ["#531516", "#12151c"],
          data      : [0, 100]
        },

      }
    },

    created() {
      return this.run()
    },

    watch: {
      working() {
        this.show.working = !(this.working === null);
        setTimeout(() => this.updateCollection(), 1000);
        this.$forceUpdate()
      },

      time() {
        if (this.time % 60 === 0)
          this.updateCollection()
      }
    },

    methods: {
      async fetchTickets() {
        this.tickets = await Salary.tickets.sum()
      },

      toggleShow(field = 'tickets') {
        if (field === 'tickets') {
          this.show.tickets.sum = !this.show.tickets.sum;
          this.show.tickets.input = !this.show.tickets.input
        }

      },

      save(sum = null) {
        Salary.tickets.update(sum || this.tickets.sum);
        this.toggleShow();
        this.tickets.sum = sum || this.tickets.sum
      },

      updateCollection() {
        let minWork = (7 * 60 * 60) + (this.working ? this.working.lunch * 60 : 0);
        this.collection.data = [(this.time / minWork) * 100, Math.min(Math.abs((1 - (this.time / minWork)) * 100), 100)]
      },

      emit() {
        if (this.show.working) {
          this.$emit('stop');
          this.show.working = false
        } else {
          this.$emit('start');
          this.show.working = true
        }
      },

      run() {
        this.fetchTickets();
        this.updateCollection();
        this.show.working = !(this.working === null);
      }
    }
  }
</script>

