<template>
  <section id="salaryContent">
    <bg-transition image="salary" title="Salaires"/>

    <div class="section-content pt-5 bg-color-5" v-if="salaries">

      <div class="row mb-5">
        <div class="col-12">
          <line-chart :chart-data="collections.salary" :options="{responsive: true, maintainAspectRatio: false}"
                      :height="300" class="bg-color-4 rounded" v-if="collections.salary"/>
        </div>


        <div class="col-12 row">

          <div class="d-inline-block amount-container full-flex flex-column bg-color-4 mr-md-3 rounded my-2 my-md-3"
               v-for="data in amounts" :class="data.cssClass">
            <div class="row amount-logo text-center mb-3 full-flex">
              <ssf-icon :icon="data.icon" size="2x" type="la"/>
            </div>
            <div class="row">
              <div class="row fa-1x5 number w-100 text-center">
                <span class="w-100 text-center">{{ data.amount }}</span>
              </div>
              <hr class="w-50 bg-white">
              <div class="row w-100">
                <label class="w-100 text-center source-sans-pro">{{ data.more.label }} <br>
                  <span class="font-weight-bold">{{ data.more.value }}</span></label>
              </div>
            </div>
          </div>

          <div
            class="d-none d-xl-inline-flex justify-content-center align-items-center flex-column bg-color-4 rounded my-2 my-md-3 bar-total-chart">
            <bar-chart :chart-data="collections.amount" :options="{label: {display:false}}" v-if="collections.amount"
                       :height="200" class="bg-color-4 rounded"/>
          </div>

        </div>

      </div>

      <div class="row mt-5">

        <div class="col-12 col-md-8">
          <ul class="w-100 bg-color-4 col-12 py-3 rounded">
            <li class="row border-bottom border-strong py-2">
              <div v-for="row in html.table.headers" :class="row.cssClass" class="text-center">
                <ssf-icon :icon="row.icon" type="fa" size="1x5" weight="light"/>
              </div>
            </li>
            <li class="row border-bottom py-2" v-for="(salary, $idx) in slicedSalaries"
                v-on:dblclick="$modal.show('modal-add-salary', { salaryProp: salary })">
              <div class="col-1 col-md-2 text-center px-0">{{ $idx + 1 }}</div>
              <div class="col-4 col-md-3 text-center">{{ helpers.date.moment(salary.date).format('MMM D YYYY') |
                capitalize }}
              </div>
              <div class="col-3 col-md-2 text-center">
                {{ helpers.number.pretty(parseInt(salary.amount)) }}
                <span class="small"><ssf-icon icon="euro-sign" type="la"/></span>
              </div>
              <div class="col-1 col-md-2 text-center px-0">{{ salary.meals_tickets }}</div>
              <div class="col-2 text-center small font-italic">
                <span class="fa-xs">{{ helpers.date.moment(salary.created_at).format('L') }}</span>
              </div>
            </li>
          </ul>
        </div>

        <div class="col-12 col-md-4 full-flex mb-5 mb-md-0">
          <div class="add-salary waves-effect waves-light waves-block rounded-circle full-flex"
               @click="$modal.show('modal-add-salary')">
            <ssf-icon icon="layer-plus" type="fa" weight="solid" size="3x"/>
          </div>
        </div>

      </div>
    </div>
    <modal-add-salary @added="onAddedSalary" @updated="run"/>
  </section>
</template>

<script>

  import Salary         from "@/app/models/salary";
  import BarChart       from "@/components/includes/commons/charts/BarChart";
  import BgTransition   from "@/components/includes/commons/partials/BgTransition";
  import LineChart      from "@/components/includes/commons/charts/LineChart";
  import ModalAddSalary from "@/components/pages/Life/components/includes/ModalAddSalary";

  import moment from 'moment'

  export default {
    name: "Salary",

    components: { BarChart, ModalAddSalary, BgTransition, LineChart },

    data() {
      return {
        salaries           : null,
        lastYearSalaries   : null,
        twoYearsAgoSalaries: null,

        collections: { salary: null, amount: null },

        amounts: null,

        html: {
          table: {
            headers: [
              { icon: 'hashtag', cssClass: 'col-1 col-md-2 px-0 blue-text' },
              { icon: 'calendar-alt', cssClass: 'col-4 col-md-3 red-text' },
              { icon: 'euro-sign', cssClass: 'col-3 col-md-2 green-text' },
              { icon: 'meat', cssClass: 'col-1 col-md-2 px-0 yellow-text' },
              { icon: 'calendar-edit', cssClass: 'col-2 blue-grey-text' },
            ],
            offset : 0,
            limit  : 12,
            max    : 12
          }
        }
      }
    },

    created() {
      return this.run()
    },

    computed: {
      slicedSalaries() {
        return this.salaries ? this.salaries.sort((a, b) => a.date < b.date ? 1 : -1).slice(this.html.table.offset, this.html.table.limit) : 0
      }
    },

    methods: {
      async fetchSalaries() {
        this.salaries = await Salary.mine() || [];
        this.lastYearSalaries = await Salary.lastYear() || [];
        this.twoYearsAgoSalaries = await Salary.twoYearsAgo() || []
      },

      createDataCollection() {
        let dataCollection = {
          salaries           : new Array(12).fill(0),
          lastYearSalaries   : new Array(12).fill(0),
          twoYearsAgoSalaries: new Array(12).fill(0),
        };
        if (this.salaries && this.lastYearSalaries && this.twoYearsAgoSalaries) {
          this.salaries.forEach(salary => dataCollection.salaries[this.helpers.date.moment(salary.date).month()] += salary.amount);
          this.lastYearSalaries.forEach(salary => dataCollection.lastYearSalaries[this.helpers.date.moment(salary.date).month()] += salary.amount);
          this.twoYearsAgoSalaries.forEach(salary => dataCollection.twoYearsAgoSalaries[this.helpers.date.moment(salary.date).month()] += salary.amount)
        }
        return this.helpers.clone(dataCollection)
      },

      createSalaryCollection() {
        let year = this.helpers.date.moment().year(),
          dataCollection = this.createDataCollection();
        this.collections.salary = {
          labels    : moment.monthsShort(),
          labelColor: '#fff',
          datasets  : [
            {
              label          : year,
              backgroundColor: 'rgba(188, 80, 144, .6)',
              borderColor    : '#bc5090',
              data           : dataCollection.salaries
            },
            { label: year - 1, fill: false, borderColor: '#003f5c', data: dataCollection.lastYearSalaries },
            { label: year - 2, fill: false, borderColor: '#ffa600', data: dataCollection.twoYearsAgoSalaries }
          ]
        }
      },

      createAmountCollection() {
        let year = this.helpers.date.moment().year();
        this.collections.amount = {
          labels  : [year, year - 1, year - 2],
          datasets: [
            {
              label          : year,
              backgroundColor: "#bc5090",
              barThickness   : 10,
              data           : [this.helpers.number.total(this.salaries, 'amount'), 0, 0]
            },
            {
              label          : year - 1,
              backgroundColor: "#003f5c",
              barThickness   : 10,
              data           : [0, this.helpers.number.total(this.lastYearSalaries, 'amount'), 0]
            },
            {
              label          : year - 2,
              backgroundColor: "#ffa600",
              barThickness   : 10,
              data           : [0, 0, this.helpers.number.total(this.twoYearsAgoSalaries, 'amount')]
            },
          ],
        }
      },

      fillAmounts() {
        let latest = this.helpers.number.latest(this.salaries, 'amount'),
          max = this.helpers.number.max(this.salaries, 'amount');
        this.amounts = {
          total : {
            amount  : this.helpers.number.total(this.salaries, 'amount'),
            icon    : 'dollar-sign',
            cssClass: 'amount-total',
            more    : { label: "Nb. mois", value: this.salaries.length }
          },
          max   : {
            amount  : parseInt(max.amount),
            icon    : 'chart-line',
            cssClass: 'amount-better',
            more    : { label: "Date", value: this.helpers.date.moment(max.date).format('L') }
          },
          latest: {
            amount  : parseInt(latest.amount),
            icon    : 'hourglass-end',
            cssClass: 'amount-last',
            more    : { label: "Date", value: this.helpers.date.moment(latest.date).format('L') }
          },
        }
      },

      updateCharts() {
        this.createSalaryCollection();
        this.fillAmounts();
        this.createAmountCollection()
      },

      async run() {
        await this.fetchSalaries();
        this.updateCharts()
      },

      onAddedSalary(salary) {
        this.salaries.push(salary);
        this.updateCharts()
      }
    }
  }
</script>

<style scoped lang="scss">

  .bar-total-chart {
    height: 210px;
    width: calc(100% - 678px);
  }

  .add-salary {
    height: 100px;
    width: 100px;
    background: -webkit-linear-gradient(40deg, #191d26, #531516) !important;
    background: linear-gradient(40deg, #191d26, #531516) !important;
  }

</style>
