<template>
  <section id="mortgageContent">

    <bg-transition image="mortgage" title="Prêt immobilier"/>

    <div class="section-content pt-5 bg-color-5 row col-12">

      <div class="col-12 col-md-6">
        <div class="bg-color-4 rounded my-2">
          <mortgage-input name="maxAmount" v-model="maxAmountFormatted" label="Emprunt maximal" :disabled="true"
                          type="text" :border="false">
            €
            <ssf-icon icon="exclamation-triangle" class="text-danger" :solid="true"
                      v-if="parseInt(maxAmount) < needed"/>
          </mortgage-input>

          <mortgage-input name="amount" v-model="amount" label="Prix de la maison" @change="updateMonthly" :step="1000">
            €
          </mortgage-input>
          <mortgage-input name="notary" v-model="notary" label="Frais de notaire">€</mortgage-input>
          <mortgage-input name="works" v-model="works" label="Travaux" :step="1000">€</mortgage-input>
          <mortgage-input name="contribution" v-model="contribution" label="Apport" :step="1000">€</mortgage-input>
          <mortgage-input name="salary" v-model="salary" label="Salaire" :step="100">€</mortgage-input>

          <div class="row bg-color-3 text-white rounded">
            <div class="col-12 col-md-3 col-xl-2 py-3 text-center text-md-left">
              <label class="mb-0">Montant à emprunter</label>
            </div>
            <div class="col-12 col-md-9 col-xl-10 full-flex">
              <span class="fa-1x5">{{ helpers.number.pretty(needed) }}€</span>
            </div>
          </div>
        </div>
      </div>

      <div class="col-12 col-md-6">
        <div class="bg-color-4 rounded my-2">
          <mortgage-input name="year" v-model="year" label="Durée (en année)" @change="onYearChange" :border="false">
            <span v-if="month % 12 !== 0" class="">{{ parseInt(month / 12) }} ans et {{ month % 12 }} mois</span>
            <span class="small">({{ parseInt(month) }} mois)</span>
          </mortgage-input>

          <mortgage-input name="estate" v-model="rate.estate" label="Taux annuel" @input="updateMonthly">%
          </mortgage-input>
          <mortgage-input name="assurance" v-model="rate.assurance" label="Taux assurance" @input="updateMonthly">%
          </mortgage-input>

          <mortgage-input name="monthly" v-model="monthly" label="Mensualités" @input="onMonthlyInput"
                          :slot-row="helpers.window.isSmall()">
            dont {{ computation.insurance }}€ d'assurance
          </mortgage-input>

          <mortgage-input name="debt" v-model="debt" label="Taux d'endettement" :disabled="true" type="text">
            <ssf-icon icon="exclamation-triangle" class="text-danger" :solid="true" v-if="parseInt(debt) >= 33"/>
            %
          </mortgage-input>

          <div class="row py-3 bg-color-3 rounded">
            <div class="text-center mx-auto full-flex">
              <span class="fa-2x font-weight-light text-white">{{ helpers.number.pretty(parseInt(total)) }}€</span>
              <span class="small  font-italic mx-1 fa-xs">
                <span class="position-absolute">+{{ helpers.number.pretty(parseInt(total - needed) + 1) }}€</span>
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>

  </section>
</template>

<script>
  import BgTransition  from "@/components/includes/commons/partials/BgTransition";
  import MortgageInput from "./includes/MortgageInput";

  export default {
    name      : "MortgageMain",
    components: { BgTransition, MortgageInput },
    data() {
      return {
        amount      : 135600,
        // amount      : 129624,
        notary      : 0,
        works       : 2000,
        contribution: 5000,
        salary      : 2000,
        rate        : {
          estate   : 1.45,
          assurance: .4,
        },
        year        : 23,
        month       : 0,
        // month    : 240,
        monthly     : 0
      }
    },

    computed: {

      computation() {
        return {
          monthly  : (((this.needed * ((this.rate.estate / 1200))) / (1 - Math.pow(1 + ((this.rate.estate / 1200)), (-1 * this.month)))) + this.insurance).toFixed(2),
          insurance: this.insurance.toFixed(2)
        }
      },

      insurance() {
        return (this.needed * ((this.rate.assurance / 1200)))
      },

      total() {
        return this.monthly * this.month
      },

      debt() {
        return parseFloat(((this.monthly / this.salary) * 100).toFixed(2))
        // return parseFloat(((this.monthly / 2000) * 100).toFixed(2))
      },

      maxAmount() {
        let monthly = 300, assurance = this.rate.assurance / 1200, estate = this.rate.estate / 1200
        while (((monthly * (1 + assurance)) / this.salary) * 100 <= 33) monthly += 1
        // while (((monthly * (1 + ((this.rate.assurance / 1200)))) / 2000) * 100 <= 33) monthly += 1
        return ((monthly * (1 + assurance)) * (1 - Math.pow(1 + estate, (-1 * this.month))) / estate)
      },

      maxAmountFormatted() {
        return this.helpers.number.pretty(parseInt(this.maxAmount)) //+ '€'
      },

      needed() {
        return parseInt(parseInt(this.amount) + parseInt(this.notary) + parseInt(this.works)) - parseInt(this.contribution)
      }
    },

    created() {
      this.month = 12 * this.year
      this.notary = parseInt(this.amount * (8 / 100))
      this.monthly = this.computation.monthly
      this.works = (this.amount * 4) / 100
    },

    watch: {
      year() {
        // this.month = 12 * this.year
        // this.monthly = this.computation.monthly
      },

      amount() {
        this.notary = parseInt(this.amount * (8 / 100))
      }
    },

    methods: {

      onYearChange() {
        this.month = 12 * this.year
        this.updateMonthly()
      },

      updateMonthly() {
        this.monthly = this.computation.monthly
        this.works = (this.amount * 4) / 100
      },

      onMonthlyInput() {
        this.month = (Math.log((((this.needed * (this.rate.estate / 1200)) / (this.monthly - this.insurance)) - 1) * -1) / Math.log((1 + (this.rate.estate / 1200)))) * -1
        this.month = this.month - parseInt(this.month) === 0 ? parseInt(this.month) : parseInt(this.month + 1)
        this.year = this.month % 12 === 0 ? this.month / 12 : parseInt(this.month / 12) + 1
      }
    }
  }
</script>

