import {MainApi} from "@/app/http/api";
import store     from "@/app/store"

const Api = new MainApi(store.getters.API_SERVER + '/api/wovalen');

class Model {
  static all(url) {
    return new Promise((resolve, reject) => {
      Api.get(url).then(
        (response) => resolve(response),
        (error) => reject(error),
      )
    })
  }

  static mine(url) {
    return new Promise((resolve, reject) => {
      Api.get(`${url}/mine`).then(
        (response) => resolve(response),
        (error) => reject(error),
      )
    })
  }

  static paginator(url, page = 0, limit = 18, options = {}) {
    return new Promise((resolve, reject) => {
      Api.get(`${url}/paginator/${page}/${limit}`, options).then(
        (data) => resolve(data),
        (error) => reject(error),
      )
    })
  }

  static store(url, data) {
    return new Promise((resolve, reject) => {
      Api.post(url, data, { alert: false }).then(
        (data) => resolve(data),
        (error) => reject(error),
      )
    })
  }

  static update(url, data, id, options = {}) {
    options.alert = false;
    return new Promise((resolve, reject) => {
      Api.put(url, id, data, options).then(
        (data) => resolve(data),
        (error) => reject(error),
      )
    })
  }

  static destroy(url, id) {
    return new Promise((resolve, reject) => {
      Api.delete(url, id, { alert: false }).then(
        (data) => resolve(data),
        (error) => reject(error),
      )
    })
  }

  static fetchById(url, id) {
    return new Promise((resolve, reject) => {
      Api.getBy(url, id).then(
        (data) => resolve(data),
        (error) => reject(error),
      )
    })
  }

  static search(url, query, options = {}) {
    return new Promise((resolve, reject) => {
      Api.getBy(`${url}/search`, encodeURIComponent(query), options).then(
        (data) => resolve(data),
        (error) => reject(error),
      )
    })
  }

  static format = {
    code   : (code) => code ? code.toUpperCase().replace(/[^a-zA-Z0-9_]+/g, '_').substr(0, 20) : code,
    account: (account) => account ? account.toUpperCase().replace(/[^a-zA-Z0-9_]+/g, '_').substr(0, 200) : account,
  };

  static check = {
    code   : (code, strict = true) => code && (strict ? !/[^a-zA-Z0-9_]+/g.test(code) : !/[^a-zA-Z0-9_ ]+/g.test(code)) && code.length <= 20 && code.trim() !== '',
    account: (account, strict = true) => account && (strict ? !/[^a-zA-Z0-9_]+/g.test(account) : !/[^a-zA-Z0-9_ ]+/g.test(account)) && account.length <= 200 && account.trim() !== '',
    label  : (label = true) => label && label.length <= 200 && label.trim() !== '',
  }
}

export default Model
