<template>
  <div class="clock bg-color-4 rounded-circle p-3">
    <div v-for="hour in 6" :class="`hour hour-${hour}`" class="bg-white"></div>
    <div class="center-empty m-auto bg-color-4 rounded-circle"></div>
    <div class="center m-auto bg-color-3 rounded-circe"></div>
    <div class="hours m-auto bg-color-3"></div>
    <div class="minutes m-auto bg-color-3"></div>
    <div class="seconds m-auto bg-color-3"></div>
    <div class="date bg-white color-1 z-depth-1 my-auto ml-auto full-flex rounded">
      {{ helpers.date.moment().date() || 0 }}
    </div>
  </div>
</template>

<script>
  export default {
    name: "Clock",

    mounted() {
      let self = this;
      $(function () {
        jQuery.fn.rotate = function (degrees) {
          $(this).css({
            '-webkit-transform': 'rotate(' + degrees + 'deg)',
            '-moz-transform'   : 'rotate(' + degrees + 'deg)',
            '-ms-transform'    : 'rotate(' + degrees + 'deg)',
            'transform'        : 'rotate(' + degrees + 'deg)'
          });
          return $(this);
        };

        let now, e = 360;

        setInterval(function () {
          now = self.helpers.date.moment().locale('fr');
          $('.clock .hours').rotate((e * (now.hours() + (now.minutes() / 60) - 0.1)) / 12);
          $('.clock .minutes').rotate((e * now.minutes()) / 60);
          $('.clock .seconds').rotate((e * now.seconds()) / 60)
        }, 1000)
      })
    }
  }
</script>

<style scoped lang="scss">
  .clock {
    position: relative;
    height: 200px;
    width: 200px;
    box-sizing: border-box;
    display: block;
    margin: auto;
    z-index: 1;

    .hour, .hours, .minutes, .seconds, .date, .center, .center-empty {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }

    .hours, .minutes, .seconds {
      -webkit-transform-origin: bottom;
      -moz-transform-origin: bottom;
      -ms-transform-origin: bottom;
      -o-transform-origin: bottom;
      transform-origin: bottom;
      -webkit-transition: all 1s;
      -moz-transition: transform 1s;
      -ms-transition: transform 1s;
      -o-transition: transform 1s;
      transition: transform 1s;
    }
  }

  .hour {
    margin: auto;
    width: 1px;
    height: 100%;
    background-color: #A9A9A9;
    z-index: 2;

    &.hour-3, &.hour-6 {
      width: 3px;
    }

    &.hour-1 {
      -webkit-transform: rotate(30deg);
      -moz-transform: rotate(30deg);
      -ms-transform: rotate(30deg);
      -o-transform: rotate(30deg);
      transform: rotate(30deg);
    }

    &.hour-2 {
      -webkit-transform: rotate(60deg);
      -moz-transform: rotate(60deg);
      -ms-transform: rotate(60deg);
      -o-transform: rotate(60deg);
      transform: rotate(60deg);
    }

    &.hour-3 {
      -webkit-transform: rotate(90deg);
      -moz-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
      -o-transform: rotate(90deg);
      transform: rotate(90deg);
    }

    &.hour-4 {
      -webkit-transform: rotate(120deg);
      -moz-transform: rotate(120deg);
      -ms-transform: rotate(120deg);
      -o-transform: rotate(120deg);
      transform: rotate(120deg);
    }

    &.hour-5 {
      -webkit-transform: rotate(150deg);
      -moz-transform: rotate(150deg);
      -ms-transform: rotate(150deg);
      -o-transform: rotate(150deg);
      transform: rotate(150deg);
    }
  }

  .clock {
    .hours {
      bottom: 40px;
      height: 40px;
      width: 3px;
      z-index: 4;
      bottom: 40px;
      height: 40px;
      width: 3px;
      z-index: 4;
    }

    .minutes {
      bottom: 60px;
      height: 60px;
      width: 3px;
      z-index: 4;
    }

    .seconds {
      bottom: 65px;
      height: 65px;
      width: 1px;
      z-index: 4;
    }

    .date {
      height: 15px;
      width: 20px;
      right: 40px;
      z-index: 3;
      font-weight: 400;
      font-family: 'Source Code Pro', monospace;
    }

    .center-empty {
      height: 100px;
      width: 100px;
      z-index: 3;
    }

    .center {
      height: 5px;
      width: 5px;
      z-index: 3;
    }
  }
</style>
