<template>
  <section id="monthlyContent">
    <bg-transition image="monthly" title="Dépenses mensuelles"/>
    <div class="section-content pt-5 bg-color-5">

      <div class="col-12 row">

        <div class="list-expense my-2 my-md-3 w-100 mr-md-3">
          <ul class="bg-color-4 py-3 rounded w-100 col-12">
            <li class="row border-bottom py-2 full-flex" v-for="(expense, $idx) in expenses"
                @dblclick="$modal.show('modal-edit-expense', {expenseProp: expense})" @updated="fillAmounts">
              <div class="col-9">
                <div class="expense-icon bg-color-2 rounded-circle d-inline-block text-center mr-2">
                  <ssf-icon :icon="expense.icon" :fw="true" type="la" size="lg"/>
                </div>
                {{ expense.label | capitalize }}
              </div>
              <div class="col-3 text-right small">
                ({{ helpers.number.pretty(parseInt(expense.amount)) }}€)
              </div>
            </li>
          </ul>
        </div>

        <div class="amount-container-group row h-100">
          <div
            class="d-inline-block amount-container full-flex flex-column bg-color-4 rounded my-2 my-md-3 w-100 amount-small"
            v-for="data in amounts" :class="data.cssClass">
            <div class="row amount-logo text-center mb-3 full-flex">
              <ssf-icon :icon="data.icon" size="2x" type="la"/>
            </div>
            <div class="row">
              <div class="row fa-1x5 number w-100 text-center">
                <span class="w-100 text-center">{{ data.amount }}</span>
              </div>
              <hr class="w-50 bg-white">
              <div class="row w-100">
                <label class="w-100 text-center source-sans-pro">{{ data.more.label }} <br>
                  <span class="font-weight-bold">{{ data.more.value }}</span></label>
              </div>
            </div>
          </div>
        </div>

      </div>

      <div class="col-12">
        <bar-chart :chart-data="collection" :options="{responsive: true, maintainAspectRatio: false}"
                   :height="300" class="bg-color-4 rounded" v-if="collection"/>
      </div>

    </div>

    <modal-edit-expense/>

  </section>
</template>

<script>
  import Expense          from "@/app/models/expense";
  import BarChart         from "@/components/includes/commons/charts/BarChart";
  import BgTransition     from "@/components/includes/commons/partials/BgTransition";
  import ModalEditExpense from "@/components/pages/Life/components/includes/ModalEditExpense";


  export default {
    name      : "Monthly",
    components: { ModalEditExpense, BarChart, BgTransition },

    data() {
      return {
        amounts   : null,
        collection: null,

        expenses: null
      }
    },

    created() {
      this.run()
    },

    methods: {
      async fetchExpenses() {
        this.expenses = await Expense.mine()
      },

      fillAmounts() {
        let random = this.expenses[Math.floor(Math.random() * this.expenses.length)]
        this.amounts = {
          total : {
            amount  : this.helpers.number.total(this.expenses, 'amount'),
            icon    : 'dollar-sign',
            cssClass: 'amount-total',
            more    : { label: "Nb. dépenses", value: this.expenses.length }
          },
          random: {
            amount  : random.amount,
            icon    : 'random',
            cssClass: 'amount-better',
            more    : { label: "Catégorie", value: random.label.capitalize() }
          }
        }
      },

      createDataCollection() {
        return this.expenses ? _.map(this.expenses, expense => expense.amount) : [];
      },

      createCollection() {
        let dataCollection = this.createDataCollection();
        this.collection = {
          labels  : _.map(this.expenses, expense => expense.label.capitalize()),
          datasets: [
            {
              label          : "Montant des dépenses",
              backgroundColor: ["#003f5c", "#2f4b7c", "#665191", "#a05195", "#d45087", "#f95d6a", "#ff7c43", "#ffa600", "#c1e7ff"],
              borderColor    : '#bc5090',
              data           : dataCollection
            }
          ]
        }
      },

      updateCharts() {
        this.createCollection();
        this.fillAmounts();
      },

      async run() {
        await this.fetchExpenses()
        this.updateCharts()
      }
    }
  }
</script>

<style scoped lang="scss">

  .amount-container-group {
    width: 436px !important;
  }

  .expense-icon {
    height: 30px;
    width: 30px;
    line-height: 30px;
  }

  @media screen and (min-width: 768px) {
    .list-expense {
      width: calc(100% - 452px) !important;
    }
  }

</style>
