/**
 * File app/helpers.js
 *
 * Ce fichier contient les outils utiles
 * au bon fonctionnement de l'app
 *
 * @author sofianeakbly
 **/

import DateJs from "@/app/utils/date";
import moment from "moment";
import store  from '../store'

import {jsonParse} from "./index";
import _           from 'lodash'

let helpers = {};

helpers.clone = (object) => jsonParse(JSON.stringify(object));
helpers.equals = (obj1, obj2) => Object.keys(obj1).length === Object.keys(obj2).length && _.filter(Object.keys(obj2).map((key) => obj1[key] === obj2[key]), (value) => !value).length === 0;
helpers.loading = (loading = true) => store.dispatch('loading', loading);

helpers.date = DateJs

helpers.number = {
  twoDigits: (number) => parseInt(number) < 10 ? `0${parseInt(number)}` : parseInt(number),
  pretty   : (x, separator = ' ') => x ? x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, separator).trim() : '?',
  total    : (arr, field = null) => arr && arr.length ? _.map(arr, item => parseInt(item[field])).reduce((a, b) => a + b) : 0,
  min      : (arr, field) => arr && arr.length ? _.reduce(arr, (a, b) => parseFloat(a[field]) < parseFloat(b[field]) ? a : b) : 0,
  max      : (arr, field) => arr && arr.length ? _.reduce(arr, (a, b) => parseFloat(a[field]) > parseFloat(b[field]) ? a : b) : 0,
  latest   : (arr, field) => arr && arr.length ? _.reduce(arr, (a, b) => moment(a.date) > moment(b.date) ? a : b) : 0
};

helpers.window = {
  isSmall: (size = 767) => window.innerWidth < size,
  width  : () => window.innerWidth,
  height : () => window.innerHeight
};

export default helpers

